/**
 * Angular wrapper for Bryntum ProjectModel
 */

import { Component, ElementRef, EventEmitter, Output, Input, SimpleChange, SimpleChanges, OnDestroy, OnInit } from '@angular/core';

import WrapperHelper from './wrapper.helper';

import { AssignmentModel, AssignmentModelConfig, AssignmentStore, AssignmentStoreConfig, DependencyModel, DependencyModelConfig, DependencyStore, DependencyStoreConfig, EventModel, EventModelConfig, EventStore, EventStoreConfig, Model, ModelConfig, RelationConfig, ResourceModel, ResourceModelConfig, ResourceStore, ResourceStoreConfig, ResourceTimeRangeModel, ResourceTimeRangeModelConfig, ResourceTimeRangeStore, ResourceTimeRangeStoreConfig, StateTrackingManager, StateTrackingManagerConfig, Store, StoreConfig, TimeRangeModel, TimeSpan, TimeSpanConfig } from '@bryntum/calendar';
import { ProjectModel, StringHelper, WidgetHelper } from '@bryntum/calendar';

@Component({
    selector : 'bryntum-project-model',
    template : ''
})
export class BryntumProjectModelComponent implements OnInit, OnDestroy {

    public static instanceClass = ProjectModel;

    public static instanceName = 'ProjectModel';

    private static bryntumEvents: string[] = [
        'onChange',
        'onDataReady'
    ];

    private static bryntumFeatureNames: string[] = [

    ];

    private static bryntumConfigs: string[] = BryntumProjectModelComponent.bryntumFeatureNames.concat([
        'assignmentModelClass',
        'assignments',
        'assignmentsData',
        'assignmentStore',
        'assignmentStoreClass',
        'children',
        'dependencies',
        'dependenciesData',
        'dependencyModelClass',
        'dependencyStore',
        'dependencyStoreClass',
        'eventModelClass',
        'events',
        'eventsData',
        'eventStore',
        'eventStoreClass',
        'expanded',
        'id',
        'json',
        'orderedParentIndex',
        'parentId',
        'parentIndex',
        'readOnly',
        'resourceModelClass',
        'resources',
        'resourcesData',
        'resourceStore',
        'resourceStoreClass',
        'resourceTimeRanges',
        'resourceTimeRangesData',
        'resourceTimeRangeStore',
        'resourceTimeRangeStoreClass',
        'silenceInitialCommit',
        'stm',
        'timeRangeModelClass',
        'timeRanges',
        'timeRangesData',
        'timeRangeStore',
        'timeRangeStoreClass',
        'timeZone'
    ]);

    private static bryntumConfigsOnly: string[] = [
        'assignmentModelClass',
        'assignmentsData',
        'assignmentStoreClass',
        'dependenciesData',
        'dependencyModelClass',
        'dependencyStoreClass',
        'eventModelClass',
        'eventsData',
        'eventStoreClass',
        'expanded',
        'orderedParentIndex',
        'parentId',
        'parentIndex',
        'resourceModelClass',
        'resourcesData',
        'resourceStoreClass',
        'resourceTimeRangesData',
        'resourceTimeRangeStoreClass',
        'silenceInitialCommit',
        'timeRangeModelClass',
        'timeRangesData',
        'timeRangeStoreClass'
    ];

    private static bryntumProps: string[] = BryntumProjectModelComponent.bryntumFeatureNames.concat([
        'allChildren',
        'assignments',
        'assignmentStore',
        'autoExposeFields',
        'children',
        'childrenField',
        'convertEmptyParentToLeaf',
        'defaults',
        'dependencies',
        'dependencyStore',
        'descendantCount',
        'events',
        'eventStore',
        'hasGeneratedId',
        'id',
        'idField',
        'inlineData',
        'internalId',
        'isCommitting',
        'isCreating',
        'isValid',
        'json',
        'previousSiblingsTotalCount',
        'readOnly',
        'relations',
        'resources',
        'resourceStore',
        'resourceTimeRanges',
        'resourceTimeRangeStore',
        'stm',
        'timeRanges',
        'timeRangeStore',
        'timeZone',
        'visibleDescendantCount'
    ]);

    private elementRef: ElementRef;
    public instance!: ProjectModel;

    private bryntumConfig = {
        adopt            : undefined,
        appendTo         : undefined,
        href             : undefined,
        angularComponent : this,
        features         : {},
        listeners        : {}
    };

    constructor(element: ElementRef) {
        this.elementRef = element;
    }

     // Configs only
    @Input() assignmentModelClass       ! : typeof AssignmentModel;
    @Input() assignmentsData            ! : AssignmentModel[]|Partial<AssignmentModelConfig>[];
    @Input() assignmentStoreClass       ! : typeof AssignmentStore;
    @Input() dependenciesData           ! : DependencyModel[]|Partial<DependencyModelConfig>[];
    @Input() dependencyModelClass       ! : typeof DependencyModel;
    @Input() dependencyStoreClass       ! : typeof DependencyStore;
    @Input() eventModelClass            ! : typeof EventModel;
    @Input() eventsData                 ! : EventModel[]|Partial<EventModelConfig>[];
    @Input() eventStoreClass            ! : typeof EventStore;
    @Input() expanded                   ! : boolean;
    @Input() orderedParentIndex         ! : number;
    @Input() parentId                   ! : string|number|null;
    @Input() parentIndex                ! : number;
    @Input() resourceModelClass         ! : typeof ResourceModel;
    @Input() resourcesData              ! : ResourceModel[]|Partial<ResourceModelConfig>[];
    @Input() resourceStoreClass         ! : typeof ResourceStore;
    @Input() resourceTimeRangesData     ! : ResourceTimeRangeModel[];
    @Input() resourceTimeRangeStoreClass! : typeof ResourceTimeRangeStore;
    @Input() silenceInitialCommit       ! : boolean;
    @Input() timeRangeModelClass        ! : typeof TimeRangeModel;
    @Input() timeRangesData             ! : TimeSpan[];
    @Input() timeRangeStoreClass        ! : typeof Store;

     // Configs and properties
    @Input() assignments           ! : AssignmentModel[]|Partial<AssignmentModelConfig>[];
    @Input() assignmentStore       ! : AssignmentStore|Partial<AssignmentStoreConfig>;
    @Input() children              ! : boolean|object[]|Model[]|Partial<ModelConfig>[];
    @Input() dependencies          ! : DependencyModel[]|Partial<DependencyModelConfig>[];
    @Input() dependencyStore       ! : DependencyStore|Partial<DependencyStoreConfig>;
    @Input() events                ! : EventModel[]|Partial<EventModelConfig>[];
    @Input() eventStore            ! : EventStore|Partial<EventStoreConfig>;
    @Input() id                    ! : string|number;
    @Input() json                  ! : string;
    @Input() readOnly              ! : boolean;
    @Input() resources             ! : ResourceModel[]|Partial<ResourceModelConfig>[];
    @Input() resourceStore         ! : ResourceStore|Partial<ResourceStoreConfig>;
    @Input() resourceTimeRanges    ! : ResourceTimeRangeModel[]|Partial<ResourceTimeRangeModelConfig>[];
    @Input() resourceTimeRangeStore! : ResourceTimeRangeStore|Partial<ResourceTimeRangeStoreConfig>;
    @Input() stm                   ! : StateTrackingManager|Partial<StateTrackingManagerConfig>;
    @Input() timeRanges            ! : TimeSpan[]|Partial<TimeSpanConfig>[];
    @Input() timeRangeStore        ! : Store|Partial<StoreConfig>;
    @Input() timeZone              ! : string|number;

     // Properties only
    @Input() allChildren               ! : Model[];
    @Input() autoExposeFields          ! : boolean;
    @Input() childrenField             ! : string;
    @Input() convertEmptyParentToLeaf  ! : boolean|object;
    @Input() defaults                  ! : object;
    @Input() descendantCount           ! : number;
    @Input() hasGeneratedId            ! : boolean;
    @Input() idField                   ! : string;
    @Input() inlineData                ! : object;
    @Input() internalId                ! : number;
    @Input() isCommitting              ! : boolean;
    @Input() isCreating                ! : boolean;
    @Input() isValid                   ! : boolean;
    @Input() previousSiblingsTotalCount! : number;
    @Input() relations                 ! : Record<string, RelationConfig>;
    @Input() visibleDescendantCount    ! : number;

     // Events
    @Output() onChange    = new EventEmitter<object>();
    @Output() onDataReady = new EventEmitter<object>();

    /**
     * Create and append the underlying widget
     */
    ngOnInit(): void {
        const
            me = this,
            {
                elementRef,
                bryntumConfig
            } = me,
            {
                instanceClass,
                instanceName,
                bryntumConfigs,
                bryntumEvents
            } = BryntumProjectModelComponent;

        bryntumConfigs.filter(prop => prop in this).forEach(prop => {
            // @ts-ignore
            WrapperHelper.applyPropValue(bryntumConfig, prop, this[prop]);
            if (['features', 'config'].includes(prop)) {
                WrapperHelper.devWarningConfigProp(instanceName, prop);
            }
        });
        // @ts-ignore
        bryntumEvents.filter(event => this[event] && this[event].observers.length > 0).forEach(event => {
            const
                uncapitalize = (str: string) => str.charAt(0).toLowerCase() + str.slice(1),
                eventName = (str: string) => uncapitalize(str.slice(2));

            // @ts-ignore
            bryntumConfig.listeners[eventName(event)] = e => {
                // @ts-ignore
                me[event].emit(e);
                // EventEmitter does not return values in the normal way, work around it by setting `returnValue` flag
                // in Angular listeners
                return e.returnValue;
            };
        });

        // If component has no container specified in config then use adopt to Wrapper's element
        const
            containerParam = [
                'adopt',
                'appendTo',
                'insertAfter',
                'insertBefore'
                // @ts-ignore
            ].find(prop => bryntumConfig[prop]);
        if (!containerParam) {
            if (instanceName === 'Button') {
                // Button should always be <a> or <button> inside owner element
                bryntumConfig.appendTo = elementRef.nativeElement;
            }
            else {
                bryntumConfig.adopt = elementRef.nativeElement;
            }
        }
        else {
            WrapperHelper.devWarningContainer(instanceName, containerParam);
        }

        // @ts-ignore
        me.instance = instanceName === 'Widget' ? WidgetHelper.createWidget(bryntumConfig) : new instanceClass(bryntumConfig);

    }

    /**
     * Watch for changes
     * @param changes
     */
    ngOnChanges(changes: SimpleChanges): void {
        const
            { instance } = this,
            { instanceName } = BryntumProjectModelComponent;
        if (!instance) {
            return;
        }
        // Iterate over all changes
        Object.entries(changes).forEach(([prop, change]) => {
            const
                newValue = (change as SimpleChange).currentValue,
                { instance } = this,
                { bryntumConfigsOnly, bryntumProps } = BryntumProjectModelComponent;
            if (bryntumProps.includes(prop)) {
                WrapperHelper.applyPropValue(instance, prop, newValue, false);
                if (bryntumConfigsOnly.includes(prop)) {
                    WrapperHelper.devWarningUpdateProp(instanceName, prop);
                }
            }
        });
    }

    /**
     * Destroy the component
     */
    ngOnDestroy(): void {
        // @ts-ignore
        this.instance && this.instance.destroy && this.instance.destroy();
    }
}
