    @Input() anchor                 ! : boolean;
    @Input() ariaDescription        ! : string;
    @Input() ariaLabel              ! : string;
    @Input() autoUpdateRecord       ! : boolean;
    @Input() bubbleEvents           ! : object;
    @Input() callOnFunctions        ! : boolean;
    @Input() centered               ! : boolean;
    @Input() config                 ! : object;
