    @Input() extraData          ! : string|number|boolean|object;
    @Input() flex               ! : number|string;
    @Input() height             ! : number|string;
    @Input() hidden             ! : boolean;
    @Input() html               ! : string|Function;
    @Input() id                 ! : string;
