/**
 * Angular wrapper for Bryntum Demo Header
 */
import { Component, ElementRef } from '@angular/core';

@Component({
    selector : 'bryntum-demo-header',
    template : `
        <header class="demo-header">
            <div id="title-container">
                <a id="title" href="{{ link }}">
                    <h1>{{ title }}</h1>
                </a>
            </div>
            <ng-content></ng-content>
            <div id="tools">
                <bryntum-button
                    text="Download Trial"
                    href="https://bryntum.com/download/?product=calendar"
                    cls="b-green b-raised"
                ></bryntum-button>
                <bryntum-fullscreen-button></bryntum-fullscreen-button>
            </div>
        </header>
`
})
export class BryntumDemoHeaderComponent {

    private elementRef: ElementRef;
    title = document.title.split(' - ')[1] || document.title;
    link = '';

    constructor(element: ElementRef) {
        this.elementRef = element;
        const match = /(.*?\/)(frameworks\/.*?)\/dist/.exec(document.location.href);
        this.link = match ? `${match[1]}#example-${match[2].replace(/\//gm, '-')}` : '#';
    }

}
