    @Output() onToolClick                 = new EventEmitter<object>();

     // Features
    @Input() cellCopyPasteFeature! : object|boolean|string|CellCopyPaste|Partial<CellCopyPasteConfig>;
    @Input() cellEditFeature  ! : object|boolean|string|CellEdit|Partial<CellEditConfig>;
